<template>

    <div class ="panels">
        <img class="logo" src="../assets/readysLogo.png">
        <div center class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel yellow black-text center">
                        <h3>Login</h3>
                        <form>
                            <div class="input-field">
                            <i class="material-icons prefix">email</i>
                                <input type="text" id="email" v-model="email">
                                <label for="email">Email</label>
                            </div>
                            <div class="input-field">
                                <i class="material-icons prefix">lock</i>
                                <input type="password" id="password" v-model="password">
                                <label for="password">Password</label>
                            </div>
                            <button v-on:click="login" class="btn btn-large grey lighten-4 black-text">Login</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import router from '../router'
import firebase from 'firebase';


export default {
    name: 'login',
    data() {
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        login(){
          
           firebase.auth().signInWithEmailAndPassword(this.email,this.password)
           .then((user) => {
                let userId = firebase.auth().currentUser.uid;
                firebase.database().ref('Users/Accounts').child(userId).once('value').then(function(snapshot){
                let accType = snapshot.child("accountType").val();
                   console.log(accType)
                   if(accType === 1){
                        console.log("TEACHER")
                        router.go({path: '/dashboard'})
                        //  router.go({ path: '/dashboard'})
                    }
                    else if(accType === 0) {
                        
                        router.go({path: '/'})
                        console.log("ADMIN")
                        
                    }else if (accType === 2){
                        console.log("STUDENT")
                    }

                });
             }).catch( function(err){
               alert(err.message)
             })
           
         }
    }
}
</script>
<style>
.panels{
    margin-right: 160px;
    margin-top: 30px;
    padding-left: 150px;
    padding-right: 150px
}

.logo{
    margin-left: 270px ;
}
</style>




