<template>
    <div id="new-words-quiz">
        <div>
            <h3>New Words Quiz</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Words:</label>
                    <input type="text" class="form-control" v-model="newItem.wordsQuiz"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/words-quiz" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-words-quiz',
    data() {
        return {
            newItem: {
                wordsQuiz: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/WordsQuiz')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            wordsQuiz: this.newItem.wordsQuiz
        })
        this.newItem.wordsQuiz = '';
        this.$router.push('/vocabulary/words-quiz')
      }
    }
}
</script>


