<template>
    <div id="words">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>List of Words</h4>
            </li>
            <div class="padd">
                <b-col md="6" class="my-1">
                    <b-form-group horizontal label="Search" class="mb-0">
                    <b-input-group>
                        <b-form-input v-model="filter" placeholder="Type to Search" />
                    </b-input-group>                    
                    </b-form-group>
                    <b-form-group horizontal label="Per page" class="mb-0">
                        <b-form-select :options="pageOptions" v-model="perPage" />
                    </b-form-group>
                </b-col>

                <div>
                    <b-table striped hover 
                        :items="items" 
                        :fields="fields" 
                        :filter="filter" 
                        @filtered="onFiltered" 
                        :options="sortOptions"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        >
                        <template slot="actions" slot-scope="row">
                            <b-button size="sm" :to="{ name: 'edit-words', params: {id: row.item['.key']} }" class="mr-1" variant="primary">
                                Edit
                            </b-button>
                            <b-button size="sm" @click="deleteItem(row.item['.key'])" onclick="alert('Are you sure you want to delete?');" class="mr-1" variant="danger">
                                Delete
                            </b-button>
                        </template>
                    </b-table>
                    <b-col md="6" class="my-1">
                        <b-pagination :total-rows="totalRows" :per-page="perPage" v-model="currentPage" class="my-0" />
                    </b-col>
                </div>

            </div>
            <div class="fixed-action-btn">
                <router-link to="/new-words" class="btn-floating btn-large red">
                    <i class="fa fa-plus"></i>
                </router-link>
            </div>
            
        </ul>
    </div>
</template>

<script>

import { db } from '../db';

export default {
    name: 'words',
    data() {
        return {
            items: [],
            fields: [
                {
                    key: 'word',
                    label: 'Words',
                    sortable: true
                },                
                {
                    key: 'actions',
                    sortable: false
                }
            ],            
            currentPage: 1,
            perPage: 10,
            pageOptions: [ 5, 10, 15 ],
            filter: null,
            sortDirection: 'asc',
            sortBy: null,
            
        }
    },
    firebase: {
        items: db.ref('Vocabulary/Words')
    },
    computed: {
        sortOptions () {
        // Create an options list from our fields
        return this.fields
            .filter(f => f.sortable)
            .map(f => { return { text: f.label, value: f.key } })
        }
    },
    methods: {
        deleteItem(key) {
            this.$firebaseRefs.items.child(key).remove();
        },
        onFiltered (filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length
            this.currentPage = 1
        }
    }
    }
</script>


