<template>
    <div id="home" style="text-decoration:none;">
        <div class="panels">
            <div class="row">
                <div class="column">
                    <router-link class="stud" to="/Students">
                        <div class="card1">
                            <i class="fa fa-users"></i>
                            <p class="p1">STUDENTS</p>
                        </div>
                    </router-link>
                </div>

                <div  class="column">
                    <router-link class="teach" to="/teachers">
                        <div class="card2">
                            <i class="fa fa-user"></i>
                            <p class="p1">TEACHERS</p>
                        </div>
                    </router-link>
                </div>

                <div class="column">
                    <router-link class="voca" to="/vocabulary">
                        <div class="card3">
                            <i class="fa fa-book"></i>
                            <p class="p1">VOCABULARY</p>
                        </div>
                    </router-link>
                </div>

                
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'home',
    data() {
        return {
        }
    }
}
</script>

<style scoped>
.fa{
    color: black;
    font-size: 15ex;
    margin-top: 30px;
}

router-link{
    text-decoration: none;
}

.stud{
    text-decoration: none;
}
.teach{
    text-decoration: none;
}
.voca{
    text-decoration: none;
}
.prof{
    text-decoration: none;
}

.p1{
    color: black;
    font: bolder;
    font-size: 20px;
    padding-top: 0px;
}
.p2{
    color: black;
    font: bolder;
    font-size: 20px;
    padding-top: 0px;
    padding-left: 30px;
}

/* Float four columns side by side */
.column {
    float: left;
    width: 25%;
    padding: 0 10px;
}

.column:hover {
    height: 400px;
}

.panels{
    padding-left: 200px;
}
/* Remove extra left and right margins, due to padding */
.row {
    margin: 0 -5px;
    padding-top: 100px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

/* Style the counter cards */
.card1 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #f78e8e;
  height: 250px;
}
.card2 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #f5f78c;
  height: 250px;
}
.card3 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #92ccf3;
  height: 250px;
}
.card4 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #93fdb7;
  height: 250px;
}
</style>



