<template>
    <div id="new-words">
        <div>
            <h3>New Words</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Words:</label>
                    <input type="text" class="form-control" v-model="newItem.word"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/words" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-words',
    data() {
        return {
            newItem: {
                word: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/Words')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            word: this.newItem.word
        })
        this.newItem.word = '';
        this.$router.push('/vocabulary/words')
      }
    }
}
</script>


