<template>
    <div id="edit-words-quiz">
        <div>
            <h3>Edit Words Quiz</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="updateItem">
                <div class="form-group">
                    <label>Words:</label>
                    <input type="text" class="form-control" v-model="newItem.wordsQuiz"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Update Item"/>
                    <router-link to="/vocabulary/words-quiz" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'edit-words-quiz',
    data() {
        return {
            newItem: {}
        }
    },
    firebase: {
        items: db.ref('Vocabulary/WordsQuiz'),
        itemsObj: {
            source: db.ref('Vocabulary/WordsQuiz'),
            asObject: true
        }
    },
    created() {
        let item = this.itemsObj[this.$route.params.id]
        this.newItem = {
            wordsQuiz: item.wordsQuiz
        }
    },
    methods: {
        updateItem() {
            this.$firebaseRefs.items.child(this.$route.params.id).set(this.newItem);
            this.$router.push('/vocabulary/words-quiz')
        }
    }
}
</script>



