<template>
    <div id="home" style="text-decoration:none;">
        <div class="panels">
        <div class="row">
            <div class="column">
                <router-link class="stud" to="/studnts">
                    <div class="card1">
                        <i class="fa fa-users"></i>
                        <p class="p1">STUDENTS</p>
                    </div>
                </router-link>
            </div>
        


            <div class="column">
                <router-link class="voca" to="/vocabulary">
                    <div class="card2">
                        <i class="fa fa-book"></i>
                        <p class="p1">VOCABULARY</p>
                    </div>
                </router-link>
            </div>

            <div class="column">
                <router-link class="prof" to="/myprofile">
                    <div class="card3">
                        <i class="fa fa-user-circle"></i>
                        <p class="p1">MY PROFILE</p>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
export default {
    name: 'dashboard',
    data() {
        return {
        }
    }
}
</script>

<style scoped>
.fa{
    color: black;
    font-size: 15ex;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
}

.stud{
    text-decoration: none;
}
.teach{
    text-decoration: none;
}
.voca{
    text-decoration: none;
}
.prof{
    text-decoration: none;
}

.panels{
    padding-left: 200px;
}

.column:hover {
    height: 400px;
}

/* Remove extra left and right margins, due to padding */
.row {
    margin: 0 -5px;
    padding-top: 100px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
.p1{
    color: black;
    font: bolder;
    font-size: 20px;
    padding-top: 0px;
}


/* Float four columns side by side */
.column {
    float: left;
    width: 25%;
    padding: 0 10px;
    /* transition */
    transition: height 4s;
    
}

.column:hover {
    height: 400px;
    -webkit-transform: scale(1.1) ;;
    -moz-transform: scale(1.1) ;
    -ms-transform: scale(1.1) ;
    -o-transform: scale(1.1) ;
    transform: scale(1.1) ;
}

/* Remove extra left and right margins, due to padding */
.row {
    margin: 0 -5px;
    padding-top: 100px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

/* Style the counter cards */
.card1 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 45px;
  text-align: center;
  background-color: #f78e8e;
  height: 250px;
}
.card2 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 45px;
  text-align: center;
  background-color: #f5f78c;
  height: 250px;
}
.card3 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 45px;
  text-align: center;
  background-color: #92ccf3;
  height: 250px;
}

router-link{
    text-decoration: none;
}
</style>



