<template>
    <div id="myProfile">
        <h1>My Profile</h1>
        <!-- <li><span class="email black-text">{{ currentUser }}</span></li> -->
        <div class="card-body">
           <h2>Welcome Teacher {{ currentUser }} !!</h2>
        </div>

    </div>
</template>

<script>
import firebase from 'firebase';
let userRef = firebase.database().ref('Users/Teacher')
export default {
    name: 'myProfile',
    data() {
        return {
            isLoggedIn : false,
            currentUser: false
        }
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true,
            this.currentUser = firebase.auth().currentUser.email;
                let userId = firebase.auth().currentUser.uid;                      
        }
    }
}
</script>


