<template>
    <div id="new-phrases-quiz">
        <div>
            <h3>New Phrases Quiz</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Phrase:</label>
                    <input type="text" class="form-control" v-model="newItem.phrasesQuiz"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/phrases-quiz" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-phrases-quiz',
    data() {
        return {
            newItem: {
                phrasesQuiz: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/PhrasesQuiz')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            phrasesQuiz: this.newItem.phrasesQuiz
        })
        this.newItem.phrasesQuiz = '';
        this.$router.push('/vocabulary/phrases-quiz')
      }
    }
}
</script>


