import Vue from 'vue'
import Router from 'vue-router'
import VueFire from 'vuefire'
import firebase from 'firebase'
import { mdbDatatable } from 'mdbvue';

import Home from '../components/Admin/Home'
import Login from '@/components/Login'

import studnts from '../components/Teacher/studnts'
import newStudnts from '../components/Teacher/newStudnts'
import editStudnts from '../components/Teacher/editStudnts'

import Students from '../components/Admin/Students'
import NewStudents from '../components/Admin/NewStudents'
import EditStudents from '../components/Admin/EditStudents'

import Teachers from '../components/Admin/Teachers'
import NewTeachers from '../components/Admin/NewTeachers'
import EditTeachers from '../components/Admin/EditTeachers'

import Vocabulary from '../components/Admin/Vocabulary'

import Alphabets from '../components/Admin/Alphabets'
import NewAlphabets from '../components/Admin/NewAlphabets'
import EditAlphabets from '../components/Admin/EditAlphabets'

import Words from '../components/Admin/Words'
import NewWords from '../components/Admin/NewWords'
import EditWords from '../components/Admin/EditWords'

import Phrases from '../components/Admin/Phrases'
import NewPhrases from '../components/Admin/NewPhrases'
import EditPhrases from '../components/Admin/EditPhrases'

import MyProfile from '@/components/MyProfile'
import Dashboard from '../components/Teacher/Dashboard'

// QUIZ

import AlphabetsQuiz from '../components/Admin/AlphabetsQuiz'
import NewAlphabetsQuiz from '../components/Admin/NewAlphabetsQuiz'
import EditAlphabetsQuiz from '../components/Admin/EditAlphabetsQuiz'

import WordsQuiz from '../components/Admin/WordsQuiz'
import NewWordsQuiz from '../components/Admin/NewWordsQuiz'
import EditWordsQuiz from '../components/Admin/EditWordsQuiz'

import PhrasesQuiz from '../components/Admin/PhrasesQuiz'
import NewPhrasesQuiz from '../components/Admin/NewPhrasesQuiz'
import EditPhrasesQuiz from '../components/Admin/EditPhrasesQuiz'


Vue.use(VueFire)
Vue.use(Router)
Vue.use(mdbDatatable)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/Students',
      name: 'Students',
      component: Students,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-students',
      name: 'new-students',
      component: NewStudents,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-students/:id',
      name: 'edit-students',
      component: EditStudents,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/teachers',
      name: 'teachers',
      component: Teachers,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-teachers',
      name: 'new-teachers',
      component: NewTeachers,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-teachers/:id',
      name: 'edit-teachers',
      component: EditTeachers,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/vocabulary',
      name: 'vocabulary',
      component: Vocabulary,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      },
      children: [
        {
          path: '/vocabulary/alphabets',
          name: 'alphabets',
          component: Alphabets,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        },
        {
          path: '/vocabulary/words',
          name: 'words',
          component: Words,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        },
        {
          path: '/vocabulary/phrases',
          name: 'phrases',
          component: Phrases,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        },
        {
          path: '/vocabulary/alphabets-quiz',
          name: 'alphabets-quiz',
          component: AlphabetsQuiz,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        },
        {
          path: '/vocabulary/words-quiz',
          name: 'words-quiz',
          component: WordsQuiz,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        },
        {
          path: '/vocabulary/phrases-quiz',
          name: 'phrases-quiz',
          component: PhrasesQuiz,
          meta: {
            requiresAdmin: true,
            requiresTeacher: false
          }
        }
      ]
    },
    {
      path: '/new-alphabets',
      name: 'new-alphabets',
      component: NewAlphabets,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-alphabets/:id',
      name: 'edit-alphabets',
      component: EditAlphabets,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-alphabets-quiz',
      name: 'new-alphabets-quiz',
      component: NewAlphabetsQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-alphabets-quiz/:id',
      name: 'edit-alphabets-quiz',
      component: EditAlphabetsQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-words',
      name: 'new-words',
      component: NewWords,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-words/:id',
      name: 'edit-words',
      component: EditWords,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-words-quiz',
      name: 'new-words-quiz',
      component: NewWordsQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-words-quiz/:id',
      name: 'edit-words-quiz',
      component: EditWordsQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-phrases',
      name: 'new-phrases',
      component: NewPhrases,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-phrases/:id',
      name: 'edit-phrases',
      component: EditPhrases,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/new-phrases-quiz',
      name: 'new-phrases-quiz',
      component: NewPhrasesQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/edit-phrases-quiz/:id',
      name: 'edit-phrases-quiz',
      component: EditPhrasesQuiz,
      meta: {
        requiresAdmin: true,
        requiresTeacher: false
      }
    },
    {
      path: '/myprofile',
      name: 'myprofile',
      component: MyProfile,
      meta: {
        requiresTeacher: true,
        requiresAdmin: false
      }
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAdmin: false,
        requiresTeacher: true
        
      }
    },{
      path: '/studnts',
      name: 'studnts',
      component: studnts,
      meta: {
        requiresTeacher: true,
        requiresAdmin: false
      }
    },
    {
      path: '/newStudnts',
      name: 'newStudnts',
      component: newStudnts,
      meta: {
        requiresTeacher: true,
        requiresAdmin: false
      }
    },
    {
      path: '/editStudnts/:id',
      name: 'editStudnts',
      component: editStudnts,
      meta: {
        requiresTeacher: true,
        requiresAdmin: false
      }
    }

  ]
})

//Nav Guard
router.beforeEach((to, from, next) => {
 
  if(to.meta.requiresAdmin || to.meta.requiresTeacher) {
    //check if not logged in
    if(!firebase.auth().currentUser){
      // go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
      
    }
     else {
      //proceed to route
      next();
      console.log('asasa')
    }
  } else if(to.meta.requiresGuest) {
     //check if logged in
     if(firebase.auth().currentUser){
      let userId = firebase.auth().currentUser.uid;
      firebase.database().ref('Users/Accounts').child(userId).once('value').then(function(snapshot){
      let accType = snapshot.child("accountType").val();
      // go to login
      if (accType ==0){
        next({
          path: '/',
          query: {
            redirect: to.fullPath
          }
        });
        console.log('invalid')
      }else if (accType ==1){
        next({
          path: '/dashboard',
          query: {
            redirect: to.fullPath
          }
        });
      }
    })
    } else {
      //proceed to route
      next();
    }
  }else if(to.meta.requiresAdmin) {
    //check if logged in
    if(firebase.auth().currentUser){
     let userId = firebase.auth().currentUser.uid;
     firebase.database().ref('Users/Accounts').child(userId).once('value').then(function(snapshot){
     let accType = snapshot.child("accountType").val();
     // go to login
     if (accType ==0){
       next({
         path: '/',
         query: {
           redirect: to.fullPath
         }
       });
       console.log('invalid')
     }else if (accType ==1){
       next({
         path: '/dashboard',
         query: {
           redirect: to.fullPath
         }
       });
     }
   })
   } else {
   
   }
 }else if(to.meta.requiresTeacher) {
  //check if logged in
    if(firebase.auth().currentUser){
      let userId = firebase.auth().currentUser.uid;
      firebase.database().ref('Users/Accounts').child(userId).once('value').then(function(snapshot){
        let accType = snapshot.child("accountType").val();
        if (accType == 1){
          next({
            path: '/dashboard',
            query: {
            redirect: to.fullPath
            }
          });
        }else if (accType == 0){
          next({
            path: '/',
            query: {
            redirect: to.fullPath
            }
          });
        }
       })
    } else {
   //proceed to route
  
 }
}
});
export default router;


