<template>
    <nav v-if="isLoggedIn">
        <div class="nav-wrapper yellow">
            <div class="container">
                <router-link to="/login" class="brand-logo black-text">
                    READys
                </router-link>
                <ul class="right">
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn blak">Logout</button></li>
                </ul>
            </div>
             
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase';

export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(() => {
                this.$router.go({ path: this.$router.path});
            })
        }
    }
}
</script>

<style scoped>
.brand-logo{
    text-decoration: none;
}
</style>




