<template>
    <div id="new-phrases">
        <div>
            <h3>New Phrases</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Phrases:</label>
                    <input type="text" class="form-control" v-model="newItem.phrase"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/phrases" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-phrases',
    data() {
        return {
            newItem: {
                phrase: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/Phrases')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            phrase: this.newItem.phrase
        })
        this.newItem.phrase = '';
        this.$router.push('/vocabulary/phrases')
      }
    }
}
</script>


