<template>
    <div id="new-alphabets">
        <div>
            <h3>New Alphabets</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Alphabets:</label>
                    <input type="text" class="form-control" v-model="newItem.alpha"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/alphabets" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-alphabets',
    data() {
        return {
            newItem: {
                alpha: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/Alphabets')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            alpha: this.newItem.alpha
        })
        this.newItem.alpha = '';
        this.$router.push('/vocabulary/alphabets')
      }
    }
}
</script>


