<template>
    <div id="new-alphabets-quiz">
        <div>
            <h3>New Alphabets Quiz</h3>
        </div>
        <div class="card-body">
            <form v-on:submit.prevent="addItem">
                <div class="form-group">
                    <label>Alphabets:</label>
                    <input type="text" class="form-control" v-model="newItem.alphaQuiz"/>
                </div>
                <div class="form-group">
                    <input type="submit" class="btn btn-primary" value="Add Item"/>
                    <router-link to="/vocabulary/alphabets-quiz" class="btn grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { db } from '../db';

export default {
    name: 'new-alphabets-quiz',
    data() {
        return {
            newItem: {
                alphaQuiz: ''
            }
        }
    },
    firebase: {
        items: db.ref('Vocabulary/AlphabetsQuiz')
    },
    methods: {
      addItem() {
        this.$firebaseRefs.items.push({
            alphaQuiz: this.newItem.alphaQuiz
        })
        this.newItem.alphaQuiz = '';
        this.$router.push('/vocabulary/alphabets-quiz')
      }
    }
}
</script>


